___CSS_LOADER_EXPORT___.push([module.id, `#malStatus,
#malTotal,
#malEpisodes,
#malUserRating,
#malRating,
#malVolumes,
#malTotalVol,
#malTotalCha,
#AddMal {
  color: white !important;
}
.mal-sync-active {
  background-color: inherit;
}
#malp select option {
  background-color: #111111;
}
#malp #malStatus,
#malp #malUserRating,
#malp #malEpisodes,
#malp #malVolumes,
#malp #malEpisodes {
  font-size: inherit;
  font-family: inherit;
  background: transparent;
  border-color: grey;
  text-decoration: none;
  outline: medium none;
  border-width: 0;
  height: auto;
  padding: 0;
  margin: 0;
  line-height: 1;
}
#malp #malEpisodes,
#malp #malVolumes,
#malp #malEpisodes {
  text-align: center;
  border-bottom-width: 1px;
}
#malp #malEpisodes:focus,
#malp #malVolumes:focus,
#malp #malEpisodes:focus {
  border-color: white !important;
}
#malSyncProgress.ms-loading {
  height: 4px;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: #ddd;
}
#malSyncProgress.ms-loading::before {
  display: block;
  position: absolute;
  content: '';
  left: -200px;
  width: 200px;
  height: 4px;
  background-color: #2980b9;
  animation: loading-mal 2s linear infinite;
}
@keyframes loading-mal {
  0% {
    left: -200px;
    width: 30%;
  }
  50% {
    width: 30%;
  }
  70% {
    width: 70%;
  }
  80% {
    left: 50%;
  }
  95% {
    left: 120%;
  }
  100% {
    left: 100%;
  }
}
#malSyncProgress.ms-done .ms-progress {
  width: 100% !important;
  background-color: #ff4081 !important;
  transition: background-color 1s !important;
}
#malp {
  display: block;
  width: 100%;
}
#malp * {
  color: white !important;
}
#malp .malp-group {
  margin-right: 10px !important;
}
#malp select {
  margin-left: 5px !important;
}
.mal-sync-active {
  border-left: 5px solid #0e1d35;
}
